.area__socialize {
  width: 100%; height: 100%;

  display: grid;

  grid-template-columns: 5% 95%;
  grid-template-rows: 100%;
}
 .hot__panel {
   width: 100%; height: 100%;
   background: #FFF;
 }
  .shell__to {
    width: 100%; height: 100%;
    max-height: 5vw;
    display: grid;
  }
   .to {
     width: 35px; height: 35px;

     background: rgba(235, 235, 235, 0.95);
     border: none;
     border-radius: 5px;

     outline: none;

     align-self: center;
     justify-self: center;
   }
    .to>span {
      font-family: 'Concert One';
      font-weight: 600;
      font-size: 25px;

      line-height: 15px;
      color: #505050;
    }
 .socialize {

 }
  .panel {

  }
   .submit {
     width: 90px; height: 45px;

     background: rgba(245, 29, 51, .8);
     border: none;
     border-radius: 10px;

     outline: none;
   }
   .submit:active {
     transform: scale(0.9);
     transition: transform .1s;
   }
    .submit>span {
      font-family: 'Concert One';
      font-weight: 500;
      font-size: 18px;

      letter-spacing: 2px;
      color: #FFF;
    }
