@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Concert+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yeon+Sung&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lemonada:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Da+2:wght@400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jura:wght@300;400;500;600;700&family=Unica+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Marmelad&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Averia+Serif+Libre:wght@700&display=swap);
body {
  width: 100%; height: 100%;
  position: fixed;

  background: rgba(235, 235, 235, 0.3);
  display: grid;
}
 * { margin: 0; padding: 0; }

#root {
  grid-column: 1;
  grid-row: 1;

  overflow-x: hidden;
  overflow-y: auto;

  width: 100%; height: 100%;
  display: grid;

  z-index: 1;
}

.area__root {
  width: 100%; height: 100%;

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: 12% 110% 10%;
}
 .header {
   grid-column: 1;
   grid-row: 1;

   width: 100%; height: 12%;
   position: fixed;

   background: rgba(255, 255, 255, 0.9);
   box-shadow: 0 0 5px rgba(50, 50, 50, 0.2);

   display: grid;

   grid-template-columns: 5% 90% 5%;
   grid-template-rows: 100%;

   z-index: 1;
 }
  .inner__header {
    grid-column: 2;
    grid-row: 1;

    display: flex;

    align-items: center;
    justify-content: space-between;
  }
  .label__freechoice {
    font-family: 'Concert One';
    font-weight: 500;
    font-size: 25px;

    line-height: 30px;
  }

  .enter {
    width: 100px; height: 50px;

    background: rgba(255, 238, 0, 1);
    border: none;
    border-radius: 10px;

    font-family: Dosis;
    font-weight: 800;
    font-size: 20px;

    letter-spacing: 2.5px;
    color: #c3a720;
    outline: none;

    align-self: start;

    transition-property: background-color;
    transition-duration: .5s;
  }

  .enter:active {
    transform: scale(0.9);
    transition: transform .1s;
  }

 .area {
   grid-column: 1;
   grid-row: 2;

   width: 100%; height: 100%;
   display: grid;

   grid-template-columns: 100%;
   grid-template-rows: 81.5% auto;
 }
  .to__do {
    width: 100%; height: 100%;

    display: grid;

    grid-template-columns: 55% 45%;
    grid-template-rows: 100%;
  }

  .form {
    grid-column: 2;
    grid-row: 1;

    width: 320px; height: 360px;

    background: #FFF;
    border-radius: 15px;
    box-shadow: 0 0 15px rgba(235, 235, 235, 0.6);

    align-self: center;
    justify-self: start;

    display: grid;

    grid-template-columns: 100%;
    grid-template-rows: 100% 0%;

    align-items: center;
    justify-items: center;
  }
   .inputs {
     width: 100%; height: 80%;
     padding: 10px 0 0 0;

     align-self: start;
     display: grid;

     grid-template-columns: 100%;
     grid-template-rows: repeat(3, 1fr)
   }
   .inp {
     width: 200px; height: 40px;

     background: none;
     border: none;
     border-bottom: 1.5px solid #eee;

     font-family: 'Baloo Da 2';
     font-weight: 500;
     font-size: 16px;
     color: #555;

     outline: none;

     align-self: center;
     justify-self: center;
   }
   .inp:focus {
     border-bottom-color: #ccc;
   }

   .secure {
     -webkit-text-security: circle;
     color: #000;
   }

   .done {
     width: 120px; height: 50px;
     background: #03fce3;
     border: none;
     box-shadow: 0 0 4px #03fce3;
     border-radius: 10px;

     outline: none;
   }
   .done:active {
     transform: scale(0.9);
     transition: transform .1s;
   }
    .done>span {
      font-family: 'Concert One';
      font-weight: 600;
      font-size: 20px;

      letter-spacing: 3px;
      text-transform: uppercase;
      color: #FFF;
    }

 .description {
   width: 75%; height: 100%;

   justify-self: center;
 }
  .description__head {
    font-family: 'Concert One';
    font-weight: 500;
    font-size: 25px;

    letter-spacing: 6px;
  }

 .footer {
   grid-column: 1;
   grid-row: 3;

   width: 100%; height: 100%;

   display: grid;
   align-items: center;
 }
  .copyright {
    width: 150px;
  }
   .label__copyright {
     font-family: Dosis;
     font-weight: 500;
     font-size: 35px;

     line-height: 35px;
     color: #333;

     display: grid;

     grid-template-columns: auto auto;
     grid-template-rows: 100%;

     align-items: center;
     justify-items: center;
   }

   .label__copyright>span {
     font-size: 20px;
     line-height: 10px;

     justify-self: start;
   }

.area__enter {
  width: 320px; height: 360px;
  background: #FFF;
  border-radius: 15px;
  box-shadow: 0 0 10px rgba(50, 50, 50, 0.15);

  align-self: center;
  justify-self: center;

  align-items: center;
  justify-items: center;

  display: grid;

  grid-template-columns: 100%;
  grid-template-rows: 27.5% 27.5% 45%
}
 .inp__enter {
   width: 200px; height: 60px;

   background: rgba(200, 200, 200, 0.25);
   border: none;
   border-radius: 10px;

   font-family: 'Baloo Da 2';
   font-weight: 500;
   font-size: 16px;
   color: #555;

   padding: 0 15px;
   outline: none;
   
   align-self: end;
   justify-self: center;
 }

 .submit__enter {
   width: 100px; height: 50px;

   background: rgba(245, 29, 51, 1);
   border: none;
   border-radius: 10px;

   outline: none;
 }
 .submit__enter:active {
   transform: scale(0.9);
   transition: transform .1s;
 }
  .submit__enter>span {
    font-family: 'Concert One';
    font-weight: 600;
    font-size: 20px;

    letter-spacing: 1px;
    color: #FFF;
  }

.area__profile {
  width: 100%; height: 100%;
  display: grid;

  grid-template-columns: 60% 40%;
  grid-template-rows: 100%;
}
 .profile {
   width: 100%; height: 100%;
   background: #FFF;

   display: grid;
 }
  .profile__label {
    width: 320px; height: 100px;

    display: grid;

    grid-template-columns: 35% 65%;
    grid-template-rows: 100%;

    align-items: center;
    justify-items: end;
  }
   .icon__label {
     width: 70px; height: 70px;
     background: #03fce3;
     border-radius: 50%;

     display: grid;

     align-items: center;
     justify-items: center;
   }
    .icon {
      width: 35px; height: 35px;
    }
   .profile_label {
     justify-self: center;
   }
    .profile_label>span {
      font-family: 'Concert One';
      font-weight: 600;
      font-size: 30px;

      text-transform: uppercase;
      letter-spacing: 6px;
      color: #feeeb1;

      line-height: 20px;
    }

 .to__socialize {
   align-self: center;
   justify-self: center;
 }

.area__socialize {
  width: 100%; height: 100%;

  display: grid;

  grid-template-columns: 5% 95%;
  grid-template-rows: 100%;
}
 .hot__panel {
   width: 100%; height: 100%;
   background: #FFF;
 }
  .shell__to {
    width: 100%; height: 100%;
    max-height: 5vw;
    display: grid;
  }
   .to {
     width: 35px; height: 35px;

     background: rgba(235, 235, 235, 0.95);
     border: none;
     border-radius: 5px;

     outline: none;

     align-self: center;
     justify-self: center;
   }
    .to>span {
      font-family: 'Concert One';
      font-weight: 600;
      font-size: 25px;

      line-height: 15px;
      color: #505050;
    }
 .socialize {

 }
  .panel {

  }
   .submit {
     width: 90px; height: 45px;

     background: rgba(245, 29, 51, .8);
     border: none;
     border-radius: 10px;

     outline: none;
   }
   .submit:active {
     transform: scale(0.9);
     transition: transform .1s;
   }
    .submit>span {
      font-family: 'Concert One';
      font-weight: 500;
      font-size: 18px;

      letter-spacing: 2px;
      color: #FFF;
    }

