body {
  width: 100%; height: 100%;
  position: fixed;

  background: rgba(235, 235, 235, 0.3);
  display: grid;
}
 * { margin: 0; padding: 0; }

#root {
  grid-column: 1;
  grid-row: 1;

  overflow-x: hidden;
  overflow-y: auto;

  width: 100%; height: 100%;
  display: grid;

  z-index: 1;
}
